import dayjs from 'dayjs'
import { MAP_KEY } from '@/config'

const QQMapWX = require('@/lib/qqmap-wx-jssdk.min.js')
const qqmapsdk = new QQMapWX({
  key: MAP_KEY
})

/**
 * dayjs
 *
 * @export
 * @param {*} date 
 * @param {string} [fmt='YYYY-MM-DD'] https://github.com/iamkun/dayjs/blob/dev/docs/zh-cn/API-reference.md#%E6%A0%BC%E5%BC%8F%E5%8C%96
 * @returns
 */
export function formatDate (date, fmt = 'YYYY-MM-DD') {
  return dayjs(date).format(fmt)
}

/**
 * 
 * @doc https://lbs.qq.com/qqmap_wx_jssdk/method-reverseGeocoder.html
 * @export
 * @param {*} location { latitude: 39.984060, longitude: 116.307520 }
 * @returns
 */
export function reverseGeocoder (location) {
  return new Promise((resolve, reject) => {
    qqmapsdk.reverseGeocoder({
      location: location,
      get_poi: 1,
      poi_options: 'policy=1;page_size=20;page_index=1',
      success: res => {
        resolve(res)
      },
      fail: err => {
        reject(err)
        uni.showToast({
          title: err.message,
          icon: 'none',
          duration: 3000
        })
      }
    })
  })
}

/**
 * 
 * @doc https://lbs.qq.com/qqmap_wx_jssdk/method-search.html
 * @export
 * @param {*} keyword 
 * @param {*} location { latitude: 39.984060, longitude: 116.307520 }
 * @returns
 */
export function mapSearch (keyword, location) {
  return new Promise((resolve, reject) => {
    qqmapsdk.search({
      keyword: keyword,
      location: location,
      page_size: 20,
      auto_extend: 0,
      success: res => {
        resolve(res)
      },
      fail: err => {
        reject(err)
        uni.showToast({
          title: err.message,
          icon: 'none',
          duration: 3000
        })
      }
    })
  })
}

/**
 * 
 *
 * @export
 * @param {*} authorizeScope scopehttps://developers.weixin.qq.com/miniprogram/dev/framework/open-ability/authorize.html
 * @param {*} modal modal
 * @returns
 */
export function setAuthorize (authorizeScope, modal) {
  return new Promise((resolve, reject) => {
    if (!modal) {
      modal = {
        title: '',
        content: '',
        confirmText: ''
      }
    }
    uni.getSetting({
      success (res) {
        // hasAuthor === undefined   
        // hasAuthor === true        
        // hasAuthor === false       
        const hasAuthor = res.authSetting[authorizeScope]
        switch (hasAuthor) {
          case undefined:
            uni.authorize({
              scope: authorizeScope,
              success: res => {
                resolve(res)
              },
              fail: err => {
                uni.showToast({
                  title: '',
                  icon: 'none',
                  duration: 3000
                })
                reject(err)
              }
            })
            break
          case true:
            resolve()
            break
          case false:
            uni.showModal({
              ...modal,
              success: res => {
                if (res.confirm) {
                  uni.openSetting({
                    success: res => {
                      if (res.authSetting[authorizeScope]) {
                        resolve(res)
                      } else {
                        reject(res)
                        uni.showToast({
                          title: '',
                          icon: 'none',
                          duration: 3000
                        })
                      }
                    },
                    fail: err => {
                      reject(err)
                      uni.showToast({
                        title: '',
                        icon: 'none',
                        duration: 3000
                      })
                    }
                  })
                } else {
                  reject(res)
                  uni.showToast({
                    title: '',
                    icon: 'none',
                    duration: 3000
                  })
                }
              },
              fail: err => {
                reject(err)
                uni.showToast({
                  title: '',
                  icon: 'none',
                  duration: 3000
                })
              }
            })
            break
        }
      },
      fail: err => {
        reject(err)
        uni.showToast({
          title: '',
          icon: 'none',
          duration: 3000
        })
      }
    })
  })
}

/**
 * 
 *
 * @export
 */
export function getLocation () {
  return new Promise((resolve, reject) => {
    const scope = 'scope.userLocation'
    const modal = {
      title: '',
      content: '',
      confirmText: ''
    }
    setAuthorize(scope, modal)
      .then(() => {
        uni.getLocation({
		  type:'gcj02',
          altitude: true,
          success: res => {
            resolve(res)
          },
          fail: err => {
            reject(err)
            uni.showToast({
              title: '',
              icon: 'none',
              duration: 3000
            })
          }
        })
      })
      .catch(err => {
        reject(err)
      })
  })
}
